
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
XTextarea,
Toast
},
data () {
return {
confrim:"",
toast:false,
orderType:0,
/*  */
tpl:{},
orderLogistics:'/orderLogistics/',
/* id */
orderId:0,
/*  */
infoData:{},
/*  */
timeMap:{1:"",2:"",3:"",4:"",5:""},
/*  */

}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/*  */
rentRange(){
let self=this;
let start=dateFormat(new Date(self.infoData.order_goods_rent_time*1000), 'YYYY-MM-DD');
let end=dateFormat(new Date(self.infoData.order_goods_return_time*1000), 'YYYY-MM-DD');
return {start,end};
},
/*  */
/*  */
timeText(){
return this.timeMap[this.infoData.rentType];
},
pay_type(){
if(this.infoData.pay_type==1){
return ""
}
if(this.infoData.pay_type==2){
return ""
}
},
returnTpl(){
if(this.infoData.order_express_type==1){
return "";
}else if(this.infoData.order_express_type==2){
return "";
}else{
return "";
}
}
},
methods:{
routerBack(){
this.$router.goBack();
},
/*  */
getOrderType(item){
if (item.order_is_delect == 0) {
if (item.orderRefundStatus == 0) {
if (item.orderStatus == 1) {
if(item.orderShippingStatus == 8){
item.orderType=8;//
}else{
item.orderType=1;//
}
} else if (item.orderStatus == 2) {
switch (item.orderShippingStatus) {
case 1:
item.orderType=2;//
break;
case 2:
item.orderType=3;//
break;
case 3:
item.orderType=4;//
break;
case 4:
item.orderType=5;//
break;
case 5:
item.orderType=14;//
break;
case 6:
item.orderType=6;//
break;
case 7:
item.orderType=7;//
break;
}
}
} else if (item.orderRefundStatus == 1) {
item.orderType=9;//
} else if (item.orderRefundStatus == 2) {
if (item.orderStatus == 2) {//
if (item.orderShippingStatus == 2) {
item.orderType=11;//
} else if (item.orderShippingStatus == 3) {
item.orderType=11;//
} else if (item.orderShippingStatus == 5) {
item.orderType=12;//
} else if (item.orderShippingStatus == 6) {
item.orderType=10;//
}
}
} else if (item.orderRefundStatus == 4) {
if (item.orderShippingStatus == 6) {
item.orderType=10;//
}
}else if(item.orderRefundStatus == 3){
item.orderType=13;//
}
}
},
Initialization(){
API.order.orderInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderId:this.orderId,
}).then((res)=>{
if(res.body.code==200){
this.infoData=res.body.data;
this.getOrderType(this.infoData);
}
});
},
/*  */
deletOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.toast=true;
self.getTypeData();
}
});
}
});

},
/*  */
payOrder(id){
let self=this;
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.toast=true;
}
});
},
/*  */
confrimOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderReceipt({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.getTypeData();
self.toast=true;
}
});
}
});
},
/*  */
confrimSettlement(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderSettle({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.getTypeData();
self.toast=true;
}
});
}
});
},
/*  */
seeSettlement(id){
window.location.href="/#/settlement?id="+id;
},
/*  */
remind(id){

}

},mounted(){

},
activated(){
this.orderId=this.$route.params.id;
this.Initialization();
}
}



