
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Scroller,
ListCompent,
},
data () {
return {
goodsList:[],
/*  */
goodsVal:"",
categoryId:"",
haveData:false,//
searchType:0,
page:1,
pullupConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
typeList:[
{
name:"",
select:true,
},
{
name:"",
select:false,
}
]
}
},

methods:{
routerBack(){
this.$router.goBack();
},
load() {
console.log(1);
if (this.haveData) {
console.log(1);
}
},
/*  */
typeCheck(index){
if(this.typeList[index].select){
return false;
}else{
this.typeList[index].select=true;
this.typeList[1-index].select=false;
}
if(this.categoryId){
API.main.searchGoods({
goods_category_id:this.categoryId,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}else{
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}
},
/*  */
Initialization(){
/* */
if(this.$route.query.categoryId){
/*  */
this.goodsVal=this.$route.query.name;
this.categoryId=this.$route.query.categoryId;
API.main.searchGoods({
goods_category_id:this.$route.query.categoryId,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}else{
/*  */
this.goodsVal=this.$route.query.goods_name;
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(this.goodsList.length==10){
this.haveData=true;
}
});
}
}
}
,mounted(){
this.Initialization();
}
,activated(){
this.Initialization();
}
}



